'use strict';

(function () {

    var GLOBAL_SPEED = 0.1,
        HYPERSPACE_RUN = true;

    App.bind('home', 'unloading', function () {
        var interval = setInterval(function () {
            GLOBAL_SPEED = (GLOBAL_SPEED + 0.1) * 1.3;
            if (GLOBAL_SPEED > 70) {
                clearInterval(interval);
            }
        }, 100);

        var home = $('.page.home'),
            select = $('.page.select');

        // select.addEventListener('animationend', function begin(e) {
        //     select.removeEventListener('animationend', begin);
        // });

        home.classList.add('exit-hyperspace');
        select.classList.add('enter-hyperspace');
    });

    App.bind('home', 'unloaded', function () {
        HYPERSPACE_RUN = false;

        var home = $('.page.home'),
            select = $('.page.select');

        home.classList.remove('visible');
        home.classList.remove('exit-hyperspace');
        select.classList.remove('enter-hyperspace');
    });

    (function (canvas) {

        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        if (canvas.getContext) {
            var context = canvas.getContext('2d'),
                width = canvas.width,
                height = canvas.height,
                starsCount = 100,
                centerRadius = 100,
                length = 10,
                stars = [];

            var createStar = function createStar() {
                var randomX = Math.random(),
                    randomY = Math.random(),
                    color = ~ ~(Math.random() * 80 + 20);

                return {
                    x: randomX * width,
                    y: randomY * height,
                    xs: (randomX * width - width / 2) / 500,
                    ys: (randomY * height - height / 2) / 500,
                    size: ~ ~(color / 20),
                    length: Math.random() * length,
                    color: 'rgb(' + color + ', ' + color + ', ' + ~ ~(color + Math.random() * 20) + ')'
                };
            };

            for (var i = 0; i < starsCount; i++) {
                stars.push(createStar());
            }

            (function loop() {
                if (HYPERSPACE_RUN) {

                    window.requestAnimationFrame(loop);
                    context.clearRect(0, 0, width, height);

                    for (var i = 0; i < starsCount; i++) {
                        var s = stars[i];
                        if (s.x < 0 || s.y < 0 || s.x > width || s.y > height) {
                            s = stars[i] = createStar();
                        }
                        context.strokeStyle = s.color;
                        context.lineWidth = s.size;
                        context.shadowBlur = 5;
                        context.lineCap = 'round';
                        context.shadowColor = "#555";
                        context.beginPath();
                        context.moveTo(s.x, s.y);
                        context.lineTo(s.x + 0.1 + s.length * s.xs * GLOBAL_SPEED, s.y + s.length * s.ys * GLOBAL_SPEED);
                        context.stroke();
                        s.x += s.xs * GLOBAL_SPEED;
                        s.y += s.ys * GLOBAL_SPEED;
                    }
                }
            })();
        }
    })($('.hyperspace'));
})();
'use strict';

(function () {

    var preloadImages = function preloadImages(urls) {
        for (var i = urls.length - 1; i >= 0; i--) {
            var img = new Image();
            img.src = urls[i];
        }
    };

    App.init = function () {

        preloadImages(['img/film-1.jpg', 'img/film-2.jpg', 'img/film-3.jpg', 'img/film-4.jpg', 'img/film-5.jpg', 'img/film-6.jpg', 'img/film-7.jpg', 'img/lock.svg', 'img/star-wars-logo.svg', 'img/starship-left.png', 'img/starship-right.png', 'img/vote-left.jpg', 'img/vote-right.jpg']);

        window.addEventListener('load', function () {
            var loader = $('section.loader');
            if (loader) {
                loader.addEventListener('transitionend', function () {
                    loader.classList.remove('visible', 'fade-out');
                });
                loader.classList.add('fade-out');
            }
            if (window.location.hash && window.location.hash.length > 2) {
                var hash = window.location.hash.substring(2),
                    page = $('.page.' + hash);

                if (page && page.classList.contains('page')) {
                    page.classList.add('visible');
                    App.call(hash, 'loading');
                    App.call(hash, 'loaded');
                    return;
                }
            }
            $('.page.home').classList.add('visible');
            App.call('home', 'loading');
            App.call('home', 'loaded');
        });
    };
})();
'use strict';

/**
 * Navigation between pages
 *
 */

(function () {

    // Bind begin button to hyperspace animation
    $('.begin').addEventListener('click', function (e) {
        App.call('home', 'unloading');
        App.call('select', 'loading');
        var select = $('.page.select');
        select.addEventListener('animationend', function begin(e) {
            select.removeEventListener('animationend', begin);
            App.call('home', 'unloaded');
            App.call('select', 'loaded');
            $('.page.select').classList.add('visible');
            App.setLocation('select');
        });
    });

    // Bind navigation elements to animations
    var bindNavigation = function bindNavigation(element) {
        element.addEventListener('click', function (e) {
            if (this.getAttribute('data-target')) {
                App.goto(this.getAttribute('data-target'), this.getAttribute('data-direction') === 'up');
            }
            e.preventDefault();
        });
    };

    var elements = $$('.navigation');
    for (var i = elements.length - 1; i >= 0; i--) {
        bindNavigation(elements[i]);
    }

    // Change page if hash change
    window.addEventListener('hashchange', App.gotoHash);
})();
'use strict';

/**
 * Episode selection
 *
 */

(function () {

    var select = {
        lastMove: 0,
        leftOffset: window.screen.width / 2 - 100,
        rightOffset: 0,
        screenScale: 100 - 100 / 1.75,
        selectCards: $('.select .cards')
    };

    select.percentDivisor = window.screen.width - select.rightOffset - select.leftOffset;

    var selectionListener = function selectionListener(event) {
        var now = new Date().getTime();
        if (select.lastMove < now - 20) {

            select.lastMove = now;
            var percent = (event.screenX - select.leftOffset) / select.percentDivisor;

            if (percent < 0) {
                percent = 0;
            } else if (percent > 1) {
                percent = 1;
            }

            select.selectCards.style.transform = 'translateZ(0) translateX(-' + percent * select.screenScale + '%)';
        }
    };

    App.bind('select', 'loading', function () {
        document.addEventListener('mousemove', selectionListener);
    });

    App.bind('select', 'unloading', function () {
        document.removeEventListener('mousemove', selectionListener);
    });
})();
'use strict';

(function () {

    var buttons = $$('.button-vote');
    var bindVote = function bindVote(elt) {
        elt.addEventListener('click', function (e) {
            for (var i = buttons.length - 1; i >= 0; i--) {
                buttons[i].innerHTML = "Je vote";
            }
            elt.innerHTML = "Vote validé !";
        });
    };

    for (var i = buttons.length - 1; i >= 0; i--) {
        bindVote(buttons[i]);
    }
})();
